import React,{useState} from 'react';

const Contact = () => {
    const [data,setdata]=useState({
        fullname:"",
        phone:"",
        email:"",
        message:""

    });

    const formSubmit=(e)=>{
        e.preventDefault();
        alert(`My name is ${data.fullname}.My phone number is ${data.phone} and my email address is ${data.email}.I want to say ${data.message}`)

    }
    const inputEvent=(event)=>{
       const {name,value}=event.target;
       setdata((predata)=>{
           return{
               ...predata,
               [name]:value
           }
       })

    }
    return (
        <div>
            <div className='my-5'>
                <h1 className="text-center">Contact us</h1>
            </div>
            <div className="container contact_div">
                <div className="row">
                    <div className="col-md-6 col-10 mx-auto">
                     <form onSubmit={formSubmit}>
                        <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">FullName</label>
                        <input type="text" class="form-control" id="exampleFormControlInput1" name="fullname" value={data.fullname} onChange={inputEvent} placeholder="Enter your full name"/>
                        </div>
                        <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Phone number</label>
                        <input type="text" class="form-control" id="exampleFormControlInput1" name="phone" value={data.phone} onChange={inputEvent} placeholder="Enter your phone number"/>
                        </div>
                        <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Email address</label>
                        <input type="email" class="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={inputEvent} placeholder="Enter your email"/>
                        </div>
                        <div class="mb-3">
                        <label for="exampleFormControlTextarea1" class="form-label">Message</label>
                        <textarea class="form-control" id="exampleFormControlTextarea1" name="message" value={data.message} onChange={inputEvent} placeholder="Message" rows="3"></textarea>
                        </div>
                        <div class="col-12">
                            <button class="btn btn-outline-primary" type="submit">Submit</button>
                        </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Contact;